main > section:not(:first-of-type) {
    background: #383838;
}

#intro {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.arrow {
    position: absolute;
    bottom: 150px;
    right: 5vw;
    font-size: 2rem;
    color: #555;
    animation: bouncing 2s ease infinite;
}

.separator {
    position: absolute;
    width: 100%;
    height: 100px;
    bottom: 0;
    left: 0;
    background-color: #383838;
    clip-path: polygon(0 0, 0% 100%, 100% 100%);
}

#intro h2 {
    color: #CDBE78;
    font-family: "Playfair Display", serif;
    font-weight: 100;
    font-size: 3.5rem;
}

#intro h2 span {
    display: block;
}

#intro p {
    font-family: 'Barlow', sans-serif;
    font-size: 1.2rem;
    color: #066163;
    margin-top: 10px;
    margin-bottom: 60px;
}

#tsparticles {
    width: 100%;
    height: 100%;
}

.floating {
    position: absolute;
    left: 10vw;
    margin-bottom: 100px;
    display: inline-block;
    border-radius: 100%;
    background-color: #f2f2f2;
    box-shadow: 0 0 20px 20px #f2f2f2;
    user-select: none;
}

.floating button {
    display: block;
    width: 100%;
    padding: 10px;
    color: black;
    text-decoration: none;
    font-family: 'Barlow', sans-serif;
    font-size: 1rem;
    text-align: center;
    border: 1px solid black;
    position: relative;
    z-index: 1;
    cursor: pointer;
}

.floating button::after {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    width: 0;
    height: 100%;
    background: #383838;
    transition: all 0.3s ease;
}

.floating button:hover {
    color: white;
    border-color: #383838;
}

.floating button:hover::after{
    width: 100%;
    left: 0;
}

.home-sec {
    padding: 40px;
    color: #f2f2f2;
    font-family: 'Barlow', sans-serif;
    position: relative;
}

.underline::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 20%;
    width: 60%;
    height: 1px;
    background-color: #555;
}

.underline::before {
    content: "";
    display: block;
    position: absolute;
    bottom: -5px;
    width: 11px;
    height: 11px;
    background: #CDBE78;
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    left: calc(50% - 6px);
    z-index: 2;
}

.home-sec h2 {
    font-size: 2rem;
    font-weight: 100;
    margin-bottom: 20px;
    color: turquoise;
}

#chart {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    margin: 20px 0;
}

#chart .row {
    padding: 20px 40px;
}

#chart p {
    margin-bottom: 20px;
}

#chart .filler {
    width: 100%;
    height: 3px;
    background: #555;
    position: relative;
}

#chart .filler::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
}

#f1::before {
    width: 90%;
    background: crimson;
}

#f2::before {
    width: 35%;
    background: blueviolet;
}

#f3::before {
    width: 70%;
    background: seagreen;
}

#icons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

#icons i {
    font-size: 2rem;
    margin: 10px;
    color: #555;
}

#icons i:hover {
    color: #f2f2f2;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
}

#portfolio > p {
    margin-bottom: 20px;
}

#portfolio .card {
    width: 100%;
    max-width: 300px;
    height: 300px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    text-decoration: none;
    color: white;
    font-family: "Barlow", sans-serif;
    margin: 3px;
}

#portfolio .card p {
    display: none;
    z-index: 1;
    position: absolute;
    font-weight: bold;
}

#portfolio .card::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease;
}

#portfolio .card:hover p {
    display: inline-block;
}

#portfolio .card:hover::after {
    background-color: rgba(17, 25, 40, 0.5);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    backdrop-filter: blur(10px) saturate(180%);
    border: 1px solid rgba(255, 255, 255, 0.125);
}

#portfolio .card img {
    height: 100%;
    width: auto;
}

#contact > p {
    margin-bottom: 20px;
}

#contact form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr) 2fr;
    gap: 5px;
    place-items: center;
    width: 900px;
}

#contact input {
    background: #555;
    border: none;
    outline: none;
    display: block;
    padding: 20px;
    color: white;
    font-size: 0.9rem;
    font-family: "Barlow", sans-serif;
    width: 100%;
    height: 100%;
    position: relative;
    border-bottom: 3px solid rgba(0, 0, 0, 0);
}

#contact input:focus {
    border-bottom: 3px solid #CDBE78;
}

#contact input[name="_subject"] {
    grid-column: 1/span 2;
}

#contact textarea {
    display: block;
    grid-column: 1/span 2;
    background: #555;
    border: none;
    outline: none;
    color: white;
    padding: 20px;
    font-size: 0.9rem;
    font-family: "Barlow", sans-serif;
    width: 100%;
    height: 100%;
    border-bottom: 3px solid rgba(0, 0, 0, 0);
}

#contact textarea:focus {
    border-bottom: 3px solid #CDBE78;
}

#contact button {
    grid-column: 2/span 1;
    width: 100%;
    padding: 10px 0;
    cursor: pointer;
    margin-top: 20px;
    background: none;
    color: turquoise;
    border: 1px solid #555;
    position: relative;
    transition: all .2s ease;
    font-family: "Barlow", sans-serif;
}

#contact button:hover {
    background: #066163;
    border: 1px solid #066163;
    color: white;
}

footer {
    width: 100%;
    height: 100px;
    overflow: hidden;
    position: relative;
}

.separator-end {
    clip-path: polygon(100% 0, 0 0, 100% 100%);
    height: 100px;
    width: 100%;
    background-color: #383838;
    position: absolute;
    top: 0;
}

@keyframes bouncing {
    0% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(30px);
    }
    100% {
        transform: translateY(0px);
    }
}

@media only screen and (min-width: 1025px) {
    #intro {
        height: calc(100vh + 100px);
    }
    .separator-end {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }
    .arrow {
        font-size: 2.5rem;
    }
    #skills {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    #skills h2 {
        grid-column: 1/span 2;
        grid-row: 1/span 1;
    }
    #skills > p {
        place-self: center;
        text-align: justify;
    }
    #skills #icons {
        grid-column: 1/span 2;
        grid-row: 3/span 1;
    }
}

@media only screen and (max-width: 688px) {
    #intro h2 {
        font-size: 2.5rem;
    }

    #chart .row {
        padding: 20px 0;
    }

    #contact form {
        grid-template-rows: repeat(3, 1fr) 2fr;
    }

    #contact input[name="name"] {
        grid-column: 1/span 2;
    }

    #contact input[name="email"] {
        grid-row: 2/span 1;
        grid-column: 1/span 2;
    }
}