header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    background: #383838;
    z-index: 100;
}

nav {
    position: fixed;
    height: calc(70vh - 50px);
    top: 100px;
    width: 100%;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    background: #383838;
    opacity: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

nav button {
    width: 50%;
    color: white;
    font-family: 'Barlow', sans-serif;
    text-decoration: none;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 1rem;
    background: none;
    border: none;
    cursor: pointer;
}

nav button:hover {
    color: turquoise;
}

nav button:not(:first-of-type)::before {
    content: '';
    display: block;
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    left: 25%;
    border-top: 1px solid #555;
}

#external {
    position: fixed;
    height: calc(30vh - 50px);
    bottom: 0;
    width: 100%;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    background: #383838;
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.icons {
    display: flex;
    width: 100%;
    justify-content: center;
}

.icons a {
    text-decoration: none;
    color: #555;
    font-size: 2rem;
    margin: 0 20px;
}

.icons a:hover {
    color: turquoise;
}

.language {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Barlow', sans-serif;
    padding: 0 0;
}

.language label {
    margin-bottom: 10px;
    color: #CDBE78;
    user-select: none;
}

#langbar {
    font-family: 'Barlow', sans-serif;
    margin-bottom: 40px;
    width: 100px;
    height: 30px;
    font-weight: bold;
    background-color: #555;
    color: white;
    padding-left: 5px;
    font-size: 1rem;
}

#responsive {
    height: 100%;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#title {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10px;
    height: 100%;
    background: #066163;
    font-family: 'Playfair Display', serif;
    text-decoration: none;
    user-select: none;
}

#title h1 {
    color: #CDBE78;
    font-size: 2.2rem;
    font-weight: 100;
}

#name {
    color: white;
    font-family: 'Barlow', sans-serif;
    font-size: 1.2rem;
}

#subtitle {
    color: turquoise;
    font-size: 0.8rem;
    margin-top: 5px;
}

#menu {
    display: flex;
    flex-direction: column;
    width: 50px;
    height: 50px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
}

#menu i {
    display: block;
    width:  45px;
    height: 2px;
    margin: 5px 0;
    background-color: white;
    transition: all 0.3s ease;
}

main {
    margin-top: 100px;
}

@media only screen and (min-width: 1025px) {
    main {
        margin-top: 0;
        margin-left: 130px;
    }

    header {
        top: 0;
        left: 0;
        height: 100vh;
        width: 130px;
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: 1fr;
        gap: 30px;
    }

    #title {
        width: 100%;
        height: 100%;
        justify-content: center;
        padding: 0;
        position: relative;
    }

    #title h1 {
        font-size: 3.5rem;
    }

    #subtitle {
        position: absolute;
        bottom: 20px;
    }

    nav {
        transform: translateX(0%);
        position: relative;
        width: 100%;
        height: 100%;
        top: 0;
        opacity: 1;
    }

    .links {
        height: auto;
        display: flex;
        width: 100%;
        height: 50px;
    }

    #external {
        opacity: 1;
        position: relative;
        transform: translateX(0%);
        height: 100%;
    }

    .icons a {
        font-size: 1.5rem;
        margin: 0 10px;
    }  

    .language {
        align-items: flex-start;
        padding-left: 10px;
    }

    .language label {
        font-size: 0.8rem;
    }

    #responsive {
        display: none;
    }
}